import React from 'react'
import PropTypes from 'prop-types'
import Card from 'react-bootstrap/Card'
import { navigate } from 'gatsby'
import _ from 'lodash'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTags } from '@fortawesome/free-solid-svg-icons'

const PostCard = ({ title, date, description, url, series, seriesNo, seriesTotalNo, tags, selectedTag }) => {
  return (
    <Card>
      <Card.Body>
        {
          series &&
          <div className={'mb-3 h5 text-secondary'}>{series} - ({seriesNo} / {seriesTotalNo})</div>
        }
        <div className={'clearfix'}>
          <span className={'float-left title h4 text-blue pointer'}
                onClick={() => navigate(url)}>
            {title}
          </span>
          <span className={'float-right'}>{date}</span>
        </div>
        {
          description &&
          <p className={'mt-2 card-text'}>
            {description}
          </p>
        }
        {
          tags && <FontAwesomeIcon icon={faTags} fixedWidth/>
        }
        {
          tags &&
          tags.map((tag, index) => {
            const className = 'badge ml-2 py-1 px-2 pointer ' + (selectedTag && selectedTag === tag ? 'badge-success' : 'badge-light')
            return (
              <span key={index} className={className}
                    onClick={() => navigate(`/tag/${_.kebabCase(tag)}`)}>
              {tag}
            </span>
            )
          })
        }
      </Card.Body>
    </Card>
  )
}

PostCard.propTypes = {
  title: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  description: PropTypes.string,
  url: PropTypes.string.isRequired,
  series: PropTypes.string,
  seriesNo: PropTypes.number,
  seriesTotalNo: PropTypes.number,
  tags: PropTypes.array,
  selectedTag: PropTypes.string,
}

export default PostCard