import React from 'react'
import { graphql } from 'gatsby'
import Sidebar from '../components/Sidebar'
import { Container, Row } from 'react-bootstrap'
import Paging from '../components/Paging'
import Content from '../components/Content'
import PostCard from '../components/PostCard'
import _ from 'lodash'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTag } from '@fortawesome/free-solid-svg-icons'

const TagTemplate = ({ data, pageContext }) => {

  const {
    totalPageNo,
    currentPageNo,
    hasPrevPage,
    hasNextPage,
    prevPagePath,
    nextPagePath,
    tag
  } = pageContext

  const { edges } = data.allMarkdownRemark
  const tagSlug = `/tag/${_.kebabCase(tag)}`

  return (
    <Container>
      <Row>
        <Sidebar selectedTag={tag} selected={'tag'}/>
        <Content>
          <p className={'title h5 pointer mb-3 text-white'}>
            <FontAwesomeIcon icon={faTag} fixedWidth/> {tag}
          </p>
          {
            edges.map(({ node }) => {
              const { title, date, template, slug, description, series, seriesNo, seriesTotalNo, tags } = node.frontmatter
              return (
                <PostCard title={title}
                          date={date}
                          description={description}
                          url={`/${template}/${slug}`}
                          series={series}
                          seriesNo={seriesNo}
                          seriesTotalNo={seriesTotalNo}
                          tags={tags}
                          selectedTag={tag}
                />
              )
            })
          }
          <Paging totalPageNo={totalPageNo}
                  currentPageNo={currentPageNo}
                  prevPagePath={prevPagePath}
                  nextPagePath={nextPagePath}
                  hasPrevPage={hasPrevPage}
                  hasNextPage={hasNextPage}
                  slug={tagSlug}
          />
        </Content>
      </Row>
    </Container>
  )
}

export const query = graphql`
  query TagTemplate($tag: String, $pageLimit: Int!, $pageOffset: Int!) {
    allMarkdownRemark(
        limit: $pageLimit,
        skip: $pageOffset,
        filter: { frontmatter: { tags: { in: [$tag] }, template: { eq: "post" }, publish: { eq: "Yes" } } },
        sort: { order: DESC, fields: [frontmatter___date, frontmatter___title] }
      ){
      edges {
        node {
          frontmatter {
            title
            date
            template
            slug
            series
            seriesNo
            seriesTotalNo
            description
            tags
          }
        }
      }
    }
  }
`

export default TagTemplate
