import React from 'react'
import { navigate } from 'gatsby'
import PropTypes from 'prop-types'
import Pagination from 'react-bootstrap/Pagination'

const Paging = ({ totalPageNo, currentPageNo, prevPagePath, nextPagePath, hasPrevPage, hasNextPage, slug = '' }) => {
  const minPageNo = currentPageNo - 4 < 1 ? 1 : currentPageNo - 4
  const maxPageNo = currentPageNo + 4 > totalPageNo ? totalPageNo : totalPageNo + 4
  let pageNos = []
  for (let i = minPageNo; i <= maxPageNo; i++) {
    pageNos.push(i)
  }
  const firstSlug = slug === '' ? '/' : slug
  const lastSlug = totalPageNo === 1 ? firstSlug : `${slug}/page/${totalPageNo}`

  return (
    <div className="d-flex justify-content-center mt-4">
      <Pagination>
        <Pagination.First onClick={() => navigate(firstSlug)}/>
        <Pagination.Prev onClick={() => navigate(prevPagePath)} disabled={!hasPrevPage}/>
        {
          pageNos.map((pageNo, index) => {
            const pageUrl = pageNo === 1 ? firstSlug : `${slug}/page/${pageNo}`
            const isActive = currentPageNo === pageNo

            return (
              <Pagination.Item key={index}
                               onClick={() => navigate(pageUrl)}
                               active={isActive}>
                {pageNo}
              </Pagination.Item>)
          })
        }
        <Pagination.Next onClick={() => navigate(nextPagePath)} disabled={!hasNextPage}/>
        <Pagination.Last onClick={() => navigate(lastSlug)}/>
      </Pagination>
    </div>
  )
}

Paging.propTypes = {
  totalPageNo: PropTypes.number.isRequired,
  currentPageNo: PropTypes.number.isRequired,
  prevPagePath: PropTypes.string.isRequired,
  nextPagePath: PropTypes.string.isRequired,
  hasPrevPage: PropTypes.bool.isRequired,
  hasNextPage: PropTypes.bool.isRequired,
  slug: PropTypes.string,
}

export default Paging